.LogIn {
  display: -ms-flexbox;
  display: flex;
  margin: 100px 0 0 500px;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 70px;
  border-color: #fbb031;
  border-style: double;
}

.LogIn form {
  -ms-flex-order: 2;
      order: 2;
  margin: 0;
  width: 300px;
}

